.preloader {
  position: fixed;
  z-index: 1000;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  -webkit-transition: all 0.4s;
  -o-transition: all 0.4s;
  transition: all 0.4s;
  opacity: 0;
  background: #161616;
  visibility: hidden;
}

.preloader__visible {
  opacity: 1;
  visibility: visible;
}

.preloader-spinner {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 50px;
  height: 50px;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

.preloader-spinner span {
  display: block;
  position: absolute;
  width: 100%;
  height: 100%;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

.preloader-spinner span:after {
  content: '';
  position: absolute;
  display: block;
  width: 100%;
  height: 100%;
  border: 2px solid transparent;
  opacity: 0;
  border-right-color: #c52f36;
  -webkit-animation: rotate linear .8s infinite;
  animation: rotate linear .8s infinite;
  border-radius: 50%;
  -webkit-transition: 0.4s;
  -o-transition: 0.4s;
  transition: 0.4s;
}

.preloader-spinner span:nth-child(2) {
  width: 80%;
  height: 80%;
}

.preloader-spinner span:nth-child(2):after {
  animation-duration: 0.6s;
  animation-delay: 0.2s;
}

.preloader-spinner span:nth-child(3) {
  width: 60%;
  height: 60%;
}

.preloader-spinner span:nth-child(3):after {
  animation-duration: 0.4s;
  animation-delay: 0.4s;
}

.preloader-spinner span:nth-child(4) {
  width: 40%;
  height: 40%;
}

.preloader-spinner span:nth-child(4):after {
  animation-duration: 0.4s;
  animation-delay: 0.6s;
}

.preloader__visible .preloader-spinner {
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}

